.a-range{

}
input[type=range] {
    -webkit-appearance: none;
    margin: 18px 0;
    width: 100%;
    pointer-events: none;
  }
  input[type=range]:focus {
    outline: none;
  }
  input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 8px;
    cursor: pointer;
    animate: 0.2s;
    background: transparent;
    border-radius: $border-radius-primary;
    border: 0;
  }
  input[type=range]::-webkit-slider-thumb {
    border: 0;
    height: 24px;
    width: 24px;
    border-radius: 50%;
    background: $bg-brand;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -9px;
    pointer-events:auto;
  }
  input[type=range]:focus::-webkit-slider-runnable-track {
    background: transparent;
  }

  
  input[type=range]::-moz-range-track {
    width: 100%;
    height: 8px;
    cursor: pointer;
    animate: 0.2s;
    background: transparent;
    border-radius: $border-radius-primary;
    border: 0;
  }
  input[type=range]::-moz-range-thumb {
    border: 0;
    height: 24px;
    width: 24px;
    border-radius: 50%;
    background: $bg-brand;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -9px;
    pointer-events:auto;
  }


  input[type=range]::-ms-track {
    width: 100%;
    height: 8px;
    cursor: pointer;
    animate: 0.2s;
    background: transparent;
    border-radius: $border-radius-primary;
    border: 0;
  }
  input[type=range]::-ms-fill-lower {
    background: transparent;
    pointer-events: none;
  }
  input[type=range]::-ms-fill-upper {
    background: transparent;
    pointer-events: none;
  }
  input[type=range]::-ms-thumb {
    border: 0;
    height: 24px;
    width: 24px;
    border-radius: 50%;
    background: $bg-brand;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -9px;
    pointer-events:auto;
  }
  input[type=range]:focus::-ms-fill-lower {
    background: transparent;
    pointer-events: none;
  }
  input[type=range]:focus::-ms-fill-upper {
    background: transparent;
    pointer-events: none;
  }